<template>
    <div class="d-flex flex-grow-1 p-0">
        <!-- left slider -->

        <!-- end left slider -->
        <!-- inbox container -->
        <div class="d-flex flex-column flex-grow-1 bg-white">
            <!-- inbox header -->

            <!-- end inbox header -->
            <!-- inbox message -->
            <div class="flex-wrap align-items-center flex-grow-1 position-relative bg-gray-50">
                <div class="position-absolute pos-top pos-bottom w-100 custom-scroll">
                    <div class="d-flex h-100 flex-column">
                        <div
                                :id="'legend'+legend.id"
                                v-for="(legend,index) in formData"
                                :key="legend.id"
                                class="d-flex flex-column border-faded border-top-0 border-left-0 border-right-0 py-3 px-3 px-sm-4 px-lg-0 mr-0 mr-lg-5 flex-shrink-0"
                        >
                            <div class="d-flex align-items-center flex-row">
                                <div class="ml-0 mr-3 mx-lg-4">
                                    <i
                                            class="fad fa-2x fa-fw"
                                            :class="{
                                      'fa-question-square': !legend.complete,
                                      'text-default':  !legend.complete,
                                      'fa-check':  !!legend.complete,
                                      'text-success':  !!legend.complete
                                    }"
                                    ></i>
                                </div>
                                <div
                                        class="fw-500 flex-1 d-flex flex-column cursor-pointer"
                                        data-toggle="collapse"
                                        :data-target="'#legend'+ legend.id+' > .js-collapse'"
                                >
                                    <div class="fs-sm">
                                        <span class="fw-900 color-black">{{legend.legend}}</span>
                                    </div>
                                    <!--<div class="fs-xs fw-300 text-dark mt-1"> GM超预算审批</div>-->
                                </div>
                                <div class="color-fusion-200 fs-sm" style="text-align: right">
                                  <span class="text-primary">
                                    <strong>{{index+1}}</strong>
                                  </span>
                                </div>
                            </div>
                            <div class="collapse js-collapse">
                                <div class="pl-lg-5 ml-lg-5 pt-3 pb-4 mt-0">
                                    <div class="row color-black panel-tag ml-0 mt-0 mb-0 fs-sm">
                                        <div class="col-12 mb-2">
                                            <field
                                                    v-for="field in legend.components"
                                                    v-on:change="updateVisibility"
                                                    :data.sync="field"
                                                    :key="field.id"
                                                    v-show="!field.hidden"
                                            ></field>
                                        </div>
                                        <div class="col-12 mb-2">
                                            <div class="dropdown-divider"></div>
                                        </div>
                                        <div class="col-6 mt-2">
                                            <a
                                                    href="javascript:void(0);"
                                                    class="btn btn-default btn-sm btn-block fw-400"
                                                    :class="{'disabled':index===0}"
                                                    v-on:click="goStep(index,index-1)"
                                            >
                                                <i class="fad fa-fw fa-angle-double-left mr-2"></i>上一条
                                            </a>
                                        </div>
                                        <div class="col-6 mt-2">
                                            <a
                                                    href="javascript:void(0);"
                                                    class="btn btn-default btn-sm btn-block fw-400"
                                                    :class="{'disabled':index===formData.length-1}"
                                                    v-on:click="goStep(index,index+1)"
                                            >
                                                <i class="fad fa-fw fa-angle-double-right mr-2"></i>下一条
                                            </a>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import Field from "../fields/Field";
    import { getProcessDefinition, getFormData, startProcess } from '@/api/LPATask'
    export default {
        name: "processCreate",
        components: {
            Field
        },
        data(){
            return{
                task: {},
                formData: []
            }
        },
        created(){
            getProcessDefinition(this.$route.query.processDefinitionId).then(response => {
                this.task = response
                getFormData(this.task.formKey).then(res => {
                    res.formData.forEach((fieldset) => {
                        fieldset.complete = false;
                        fieldset.components.forEach((field, index) => {
                            field.hidden = index > 0;
                            field.invalid = false;
                        });
                    });
                    this.formData = res.formData

                    if (myapp_config.thisDevice === "desktop") {
                        $(
                            ".custom-scroll:not(.disable-slimscroll) >:first-child"
                        ).slimscroll({
                            height: $(this).data("scrollHeight") || "100%",
                            size: $(this).data("scrollSize") || "4px",
                            position: $(this).data("scrollPosition") || "right",
                            color: $(this).data("scrollColor") || "rgba(0,0,0,0.6)",
                            alwaysVisible: $(this).data("scrollAlwaysVisible") || false,
                            distance: $(this).data("scrollDistance") || "4px",
                            railVisible: $(this).data("scrollRailVisible") || false,
                            railColor: $(this).data("scrollRailColor") || "#fafafa",
                            allowPageScroll: false,
                            disableFadeOut: false,
                        });
                    }
                })
            })

            this.$on("submit", () => {
                var valid = this.validate(this.formData);
                if (!valid) {
                    return;
                }

                var allFields = [].concat.apply(
                    [],
                    this.formData.map((fieldset) => fieldset.components)
                );

                // name: "involved_user",
                //                   type: "string",
                //                   value: involvedUserId

                var variables = allFields.map((field) => {
                    return {
                        name: field.key,
                        value: field.value,
                    };
                });
                var fieldValue = {};
                allFields.forEach((field) => {
                    fieldValue[field.key] = field.value;
                });
                var wholeVariable = {
                    data: fieldValue,
                    fields: JSON.parse(JSON.stringify(this.formData)),
                };
                variables.push({
                    name: this.task.formKey,
                    value: JSON.stringify(wholeVariable),
                });

                const data = {
                    processDefinitionId: this.$route.query.processDefinitionId,
                    businessKey: this.$route.query.businessKey,
                    variables: variables
                }

                startProcess(data).then(res => {
                    this.$parent.$emit("message", {
                        type: "success",
                        title: "启动成功",
                        message: "流程启动成功。",
                    });
                    this.$router.push({ name: "home" });
                }).catch((err) => {
                    var exception = err.exception
                    exception = exception.replace('problem evaluating script:', '')
                    exception = exception.replace('javax.script.ScriptException:', '')
                    exception = exception.replace('org.activiti.engine.ActivitiIllegalArgumentException:', '')
                    this.$parent.$emit("message", {
                        type: "error",
                        title: "启动失败",
                        message: exception.trim(),
                    });
                })
            });
        },
        methods:{
            validate(fieldsets) {
                var result = true;

                fieldsets.forEach((fieldset) => {
                    var currentResult = true;
                    var fields = fieldset.components;
                    var valid;
                    fields.forEach((field) => {
                        if (field.type === "file") {
                            valid =
                                typeof field.value === "string" &&
                                field.value &&
                                JSON.parse(field.value).length > 0;
                        } else {
                            valid = !!field.value;
                        }
                        field.invalid = !field.hidden && field.validate.required && !valid;
                        if (field.invalid) {
                            result = false;
                            currentResult = false;
                        }
                    });
                    fieldset.complete = currentResult;
                });

                if (!result) {
                    this.$parent.$emit("message", {
                        type: "error",
                        title: "填写表单",
                        message: "表单信息填写不完整。",
                    });
                }

                return result;
            },
            updateVisibility(field) {
                var components = this.formData.find(
                    (fieldset) => fieldset.components.indexOf(field) >= 0
                ).components;
                if (field === components[0]) {
                    var hidden = field.value !== "存在问题";
                    components.slice(1).forEach((component) => (component.hidden = hidden));
                }
            },
            goStep(current, target) {
                console.log(current)
                console.log(target)
                if (target < 0 || target > this.formData.length - 1) {
                    return;
                }
                var fields = this.formData[current].components;
                var valid = this.validate([this.formData[current]]);
                if (valid) {
                    var fieldsets = $(".position-absolute .js-collapse");
                    fieldsets.eq(current).collapse().hide();
                    fieldsets.eq(target).collapse().show();
                }
            }
        }
    }
</script>

