import request from '@/utils/request'
import config from '@/common/config'

export function getHistoryTaskList(params) {
    return request({
        url: config.bpmnUrl + 'history/historic-task-instances',
        method: 'get',
        params
    })
}

export function getAHistoryTask(params) {
    return request({
        url: config.bpmnUrl + 'history/historic-task-instances/'+params,
        method: 'get',
        params
    })
}

export function getDetail(data) {
    return request({
        url: config.bpmnUrl + 'query/historic-variable-instances?size=1000',
        method: 'post',
        data
    })
}

export function getProcessDefinition(params) {
    return request({
        url: config.bpmnUrl + 'form/form-data?processDefinitionId=' + params,
        method: 'get',
        params
    })
}

export function getFormData(params) {
    return request({
        url: config.formUrl + params,
        method: 'get',
        params
    })
}

export function startProcess(data) {
    return request({
        url: config.bpmnUrl + 'runtime/process-instances',
        method: 'post',
        data
    })
}
